import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';

const JoinStyle = styled.div`
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
  background: #fff;
  border: 2px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;

  h2 {
    text-align: center;
    margin-bottom: 20px;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  input {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
  }

  button {
    padding: 10px;
    background-color: lightgreen;
    color: black;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
    border: 2px solid green;
  }

  #verificationSend {
    margin-bottom: 10px;
  }
  #verify{
    margin-bottom: 10px;
  }

  button:hover {
    background-color: green;
  }
`;
  // keyframes 
  const spin = keyframes`
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  `;

const VerificationButton = styled.button`
  padding: 10px;
  background-color: ${({ disabled }) => (disabled ? '#d3d3d3' : 'lightgreen')};
  color: black;
  border: none;
  border-radius: 4px;
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};
  font-size: 16px;
  transition: background-color 0.3s ease;
  border: 2px solid ${({ disabled }) => (disabled ? '#a9a9a9' : 'green')};
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: ${({ disabled }) => (disabled ? '#d3d3d3' : 'green')};
}

span {
  display: flex;
  align-items: center;

  .spinner {
    border: 2px solid lightgray;
    border-top: 2px solid green;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    animation: ${spin} 1s linear infinite;
    margin-right: 5px;
  }

  .text {
    font-size: 16px;
  }
}
`;

const Join = () => {
  const [formData, setFormData] = useState({
    userId: '',
    userPw: '',
    confirmPassword: '',
    userEmail: '',
  });

  const [isIdDuplicate, setIsIdDuplicate] = useState(null);
  const [isVerificationSent, setIsVerificationSent] = useState(false);
  const [isSendingVerification, setIsSendingVerification] = useState(false);
  const [serverCode, setServerCode] = useState('');
  const [inputCode, setInputCode] = useState('');
  const [isVerified, setIsVerified] = useState(false);

  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const checkDuplication = async () => {
    const { userId } = formData;
  
    if (!userId) {
      alert(' .');
      return false;
    }
  
    try {
      const response = await axios.post('http://116.121.53.142:9100/member/checkUsername/', { userId });
      if (response.data.isDuplicate) {
        setIsIdDuplicate(true);
        alert('  .');
        return false;
      } else {
        setIsIdDuplicate(false);
        alert('  !');
        return true;
      }
    } catch (error) {
      alert('     .');
      return false;
    }
  };
  const sendVerification = async () => {
    const { userEmail } = formData;
  
    if (!userEmail) {
      alert(' .');
      return;
    }
    setIsSendingVerification(true);
    try {
      const response = await axios.post('http://116.121.53.142:9100/member/sendVerification', { userEmail });
      if (response.status === 200) {
        console.log(response.data);
        const { verificationCode } = response.data;
        setServerCode(verificationCode);
        console.log(serverCode);
        alert('   .');
        setIsVerificationSent(true);
      }
    } catch (error) {
      alert('     .');
    } finally {
      setIsSendingVerification(false);
    }
  };

  const verifyCode = () => {
    console.log("  : " + inputCode + "  : " + serverCode);
    if (inputCode === serverCode) {
      alert(' .');
      setIsVerified(true);
    } else {
      alert('   .');
    }
  };

  const validateForm = () => {
    const { userId, userPw, userEmail } = formData;
  
    const idRegex = /^[a-zA-Z0-9]{4,12}$/;
    const pwRegex = /^(?=.*[a-zA-Z])(?=.*\d).{8,}$/;
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  
    if (!idRegex.test(userId)) {
      alert(' 4~12     .');
      return false;
    }
  
    if (!pwRegex.test(userPw)) {
      alert('  8 , , ,   .');
      return false;
    }
  
    if (!emailRegex.test(userEmail)) {
      alert('   .');
      return false;
    }
  
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) {
      return; //     
    }

    const { userId, userPw, confirmPassword, userEmail } = formData;
    if (isIdDuplicate){
      alert("   .");
      return;
    }

    if (userPw !== confirmPassword) {
      alert('  .');
      return;
    }

    if (!isVerified) {
      alert('  .');
      return;
    }

    const requestData = {
      userId,
      userPw,
      userEmail,
    };

    try {
      const response = await axios.post('http://116.121.53.142:9100/member/signUp', requestData);
      if (response.status === 200) {
        alert(' !');
        if (window.confirm('  ?')) {
          navigate('/login');
        } else {
          navigate('/');
        }
      }
    } catch (error) {
      alert('   .');
    }
  };

  return (
    <JoinStyle>
      <h2></h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="userId"
          placeholder=""
          value={formData.userId}
          onChange={handleChange}
          required
        />
        <button type="button" id="duplicatedCheckBtn" onClick={checkDuplication}>
          
        </button>
        {isIdDuplicate === true && <p style={{ color: 'red' }}>  .</p>}
        {isIdDuplicate === false && <p style={{ color: 'green' }}>  !</p>}

        <input
          type="password"
          name="userPw"
          placeholder=""
          value={formData.userPw}
          onChange={handleChange}
          required
        />
        <input
          type="password"
          name="confirmPassword"
          placeholder=" "
          value={formData.confirmPassword}
          onChange={handleChange}
          required
        />
        <input
          type="email"
          name="userEmail"
          placeholder=""
          value={formData.userEmail}
          onChange={handleChange}
          required
        />
        <VerificationButton
          id="verificationSend"
          onClick={sendVerification}
          disabled={isSendingVerification}
        >
          {isSendingVerification ? (
            <span>
              <span className="spinner" />
              <span className="text"> ...</span>
            </span>
          ) : (
            " "
          )}
        </VerificationButton>

        {isVerificationSent && (
          <>
            <input
              type="text"
              name="verificationCode"
              placeholder="  "
              value={inputCode}
              onChange={(e) => setInputCode(e.target.value)}
              required
            />
            <button type="button" id="verify" onClick={verifyCode}>
              
            </button>
          </>
        )}

        <button type="submit"></button>
      </form>
    </JoinStyle>
  );
};

export default Join;
