import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Controlled as CodeMirror } from 'react-codemirror2'; // react-codemirror2 import
import 'codemirror/lib/codemirror.css'; // Codemirror 
import 'codemirror/mode/javascript/javascript'; // JavaScript  
const ContentWrapper = styled.div`
  flex: 1;
  padding: 0;
  margin-top: 0px;  //    ( )
  display: flex;
  flex-direction: column;
`;

const EditorWrapper = styled.div`
  flex: 1;  //      
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-right: 0px;
  overflow: auto;  //    
  height: 100%;  //   100%  
`;

const CodeMirrorWrapper = styled.div`
  width: 100%;
  height: 100%;
  .CodeMirror {
    height: 100%;  // CodeMirror     
  }
`;

const MainContent = ({ fileContent, fileNo ,data }) => {
  const [code, setCode] = useState('');
  const [showLineNumbers, setShowLineNumbers] = useState(false);  //     
  const userNo = data.user.userNo;

  console.log("? " + fileNo);

  useEffect(() => {
    if (fileContent) {
      setCode(fileContent);
      setShowLineNumbers(true);  //     
    } else {
      setShowLineNumbers(false);  //     
    }
  }, [fileContent]);

  useEffect(() => {
    if (fileNo && code) {
      localStorage.setItem(fileNo.toString(), code); // fileNo  
    }
  }, [fileNo, code]);

  const handleCodeChange = (newCode) => {
    setCode(newCode);
  };

  return (
    <ContentWrapper>
      <EditorWrapper>
        {/* CodeMirror  div */}
        <CodeMirrorWrapper>
          <CodeMirror
            value={code}
            options={{
              mode: 'javascript', //   
              lineNumbers: showLineNumbers,  //       
              theme: 'default',   //  
              readOnly: false,    //   
              lineWrapping: true, //   
              viewportMargin: 10, //     
            }}
            onBeforeChange={(editor, data, value) => handleCodeChange(value)} //    
          />
        </CodeMirrorWrapper>
      </EditorWrapper>
    </ContentWrapper>
  );
};

export default MainContent;
