import React, { useState } from 'react'; 
import SidebarLeft from './SidebarLeft'; 
import SidebarRight from './SidebarRight'; 
import MainContent from './MainContent'; 
import styled from 'styled-components';
import Header from '../Layout/Header';
import Footer from '../Layout/Footer';

const MainContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;  //   
`;

const ContentWrapper = styled.div`
  display: flex;
  flex: 1;
  padding-top: 60px;  //    
  padding-bottom: 60px;  //    
  overflow: auto;  //    
`;

const CodeSyncMain = ({data}) => {
  const [fileContent, setFileContent] = useState('');
  const [fileNo, setFileNo] = useState(null);

  const handleFileContentChange = ({ content, fileNo }) => {
    setFileContent(content);
    setFileNo(fileNo); // fileNo  
  };


  return (
    <MainContainer>
      <Header />
      <ContentWrapper>
        <SidebarLeft
          onFileContentChange={handleFileContentChange}
          data={data}
        />
        <MainContent fileContent={fileContent} fileNo={fileNo} data={data} />
        <SidebarRight />
      </ContentWrapper>
      <Footer />
    </MainContainer>
  );
};


export default CodeSyncMain;
